body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 150vh;

  max-width: 100%;
  max-height: 25%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-main{
  min-width: 100%;
  max-width: 100%;
  background-color: green ; 
  border: 4px solid black;
  color: white;
  padding: 2.00rem 2.25rem;
  border-radius: 1.50rem;
}


@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}
